const rules = {
  userLowPrivileges: {
    static: [
      "clients:read",
      "activities:read",
      "representatives:read",
    ],
    dynamic: {
      "activities:edit": ({ userEmail, clientOwnerEmail }) => {
        if (!userEmail || !clientOwnerEmail) return false;
        return userEmail === clientOwnerEmail;
      },
      "clients:edit": ({ userEmail, clientOwnerEmail }) => {
        if (!userEmail || !clientOwnerEmail) return false;
        return userEmail === clientOwnerEmail;
      },
    },
  },
  userHighPrivileges: {
    static: [
      "clients:read",
      "clients:edit",
      "activities:read",
      "activities:edit",
      "representatives:read",
      "representatives:edit",
      "logs:read",
    ],
  },
  admin: {
    static: [
      "clients:read",
      "clients:edit",
      "activities:read",
      "activities:edit",
      "representatives:read",
      "representatives:edit",
      "logs:read",
      "adminDashboard:read"
    ],
  },
};

export default rules;

